<!-- button.js  js,html,style  button.vue-->
<template>
    <!--icon-${iconPosition}-->
    <button class="button" :class="{[`icon-${iconPosition}`]: true}" @click="$emit('click1')">
        <!--classiconv-ifv-bindicon-->
        <!--v-if="icon&&!loading"loadingiconsvg-->
        <!--:name="icon"iconsvg-->
        <Icon v-if="icon&&!loading" class="icon" :name=icon></Icon>

        <!--loadingname:name-->
        <Icon v-if="loading" class="loading icon" name="loading"></Icon>
        <div class="content">
            <slot></slot>
        </div>
    </button>
</template>
<script>
    // Icon
    import Icon from './icon'

    export default {
        name:"WheelButton",

        components:{Icon},
        props: {
            icon: {},
            loading:{
                type:Boolean,
                default: false
            },
            iconPosition: {
                type: String,
                default: 'left',

                // 
                validator(value) {
                    return ['left','right'].indexOf(value)>=0
                    // return value === 'left' || value === 'right'
                }
            },
        },
    }
</script>
<style lang="scss" scoped>
    @import "src/source/var";
    @keyframes spin {
        0%{transform: rotate(0deg);}
        100%{transform: rotate(360deg);}
    }

    .button { font-size: $font-size; height: $button-height; padding: 0 1em;
        border-radius: $border-radius; border: 1px solid $border-color;
        background: $button-bg;
        display: inline-flex; justify-content: center; align-items: center;
        &:hover { border-color: $border-color-hover; }
        &:active { background-color: $button-active-bg; }
        &:focus { outline: none; }

        /* icon  content icon content */
        > .content { order: 2; }
        > .icon { order: 1; margin-right: .1em; }

        &.icon-right {
            > .content { order: 1; }
            > .icon { order: 2; margin-right: 0; margin-left: .1em;}
        }

        /*loading*/
        .loading{
            animation: spin 1s infinite linear;
        }
    }
</style>